* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 4em;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.5);
  font-family: Arial, Helvetica, sans-serif;
}
.navbar h2 {
  font-size: 2.5em;
}
.navbar h4 {
  position: absolute;
  right: 0;
  margin-right: 3em;
}/*# sourceMappingURL=Navbar.css.map */